.article-link {
  display: inline-block;
  font-size: 13px;
  color: #667eea;
  text-decoration: none;
  margin-top: 5px;
  transition: color 0.2s;
}

.article-link:hover {
  color: #a78bfa;
}

.article-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
  padding-top: 10px;
  border-top: 1px solid #3d3d5c;
}

.article-delete {
  padding: 6px 12px;
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.3);
  border-radius: 6px;
  color: #f87171;
  font-size: 12px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.article-delete:hover {
  background: rgba(239, 68, 68, 0.2);
  border-color: #f87171;
}

/* View Toggle */
.view-toggle {
  display: flex;
  gap: 5px;
  margin-right: 15px;
  background: #2d2d44;
  padding: 4px;
  border-radius: 8px;
}

.view-toggle button {
  padding: 8px 16px;
  background: transparent;
  border: none;
  color: #888;
  cursor: pointer;
  border-radius: 6px;
  font-size: 13px;
  font-weight: 600;
  transition: all 0.2s;
  white-space: nowrap;
}

.view-toggle button.active {
  background: #667eea;
  color: white;
  box-shadow: 0 2px 8px rgba(102, 126, 234, 0.3);
}

.view-toggle button:hover:not(.active) {
  color: #e0e0e0;
  background: rgba(102, 126, 234, 0.1);
}

/* List View */
.list-view-container {
  flex: 1;
  padding: 40px;
  overflow-y: auto;
}

.list-view-container h2 {
  font-size: 32px;
  margin-bottom: 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.list-view-header {
  background: rgba(102, 126, 234, 0.1);
  border: 1px solid rgba(102, 126, 234, 0.3);
  border-radius: 8px;
  padding: 15px 20px;
  margin-bottom: 25px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.list-view-header span {
  color: #888;
  font-size: 14px;
}

.list-view-header strong {
  color: #a78bfa;
  font-size: 16px;
}

.entities-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-height: calc(100vh - 300px);
  overflow-y: auto;
  overflow-x: hidden;
  padding-right: 10px;
}

.entity-list-item {
  background: #1a1a2e;
  border: 1px solid #2d2d44;
  border-radius: 8px;
  padding: 15px 20px;
  cursor: pointer;
  transition: all 0.2s;
}

.entity-list-item:hover {
  border-color: #667eea;
  transform: translateX(5px);
}

.entity-list-item.selected {
  border-color: #a78bfa;
  border-width: 2px;
  background: rgba(167, 139, 250, 0.1);
}

.entity-list-name {
  font-size: 16px;
  font-weight: 600;
  color: #e0e0e0;
  margin-bottom: 8px;
}

.entity-list-meta {
  display: flex;
  gap: 15px;
  font-size: 13px;
}

.entity-list-type {
  color: #888;
  text-transform: capitalize;
}

.entity-list-connections {
  color: #a78bfa;
  font-weight: 600;
}

/* NodeDetails Enhancements */
.node-context {
  font-size: 14px;
  line-height: 1.6;
  color: #b8b8d1;
  background: rgba(102, 126, 234, 0.05);
  padding: 12px;
  border-radius: 8px;
  border-left: 3px solid #667eea;
}

.source-articles-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 10px;
}

.source-article-item {
  display: flex;
  flex-direction: column;
  gap: 5px;
  padding: 10px;
  background: rgba(102, 126, 234, 0.05);
  border: 1px solid #2d2d44;
  border-radius: 8px;
  transition: all 0.2s;
}

.source-article-item:hover {
  border-color: #667eea;
  transform: translateX(3px);
}

.source-article-link {
  font-size: 13px;
  color: #a78bfa;
  text-decoration: none;
  font-weight: 500;
  line-height: 1.4;
}

.source-article-link:hover {
  color: #c4b5fd;
  text-decoration: underline;
}

.source-article-date {
  font-size: 12px;
  color: #888;
}

.source-articles-more {
  text-align: center;
  padding: 8px;
  color: #888;
  font-size: 12px;
  font-style: italic;
}

.relationships-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 10px;
}

.relationship-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px;
  background: rgba(102, 126, 234, 0.05);
  border: 1px solid #2d2d44;
  border-radius: 6px;
  gap: 10px;
}

.relationship-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
  flex: 1;
}

.relationship-type {
  font-size: 11px;
  color: #888;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.relationship-entity {
  font-size: 14px;
  color: #e0e0e0;
  font-weight: 500;
}

.relationship-strength {
  font-size: 13px;
  color: #a78bfa;
  font-weight: 700;
  min-width: 40px;
  text-align: right;
}

.relationships-more {
  text-align: center;
  padding: 8px;
  color: #888;
  font-size: 12px;
  font-style: italic;
  margin-top: 5px;
}

/* Clickable entity in insights */
.clickable-entity {
  transition: all 0.2s;
  border-radius: 8px;
  padding: 12px !important;
}

.clickable-entity:hover {
  background: rgba(102, 126, 234, 0.15);
  transform: translateX(5px);
  border-left: 3px solid #667eea;
}

/* Articles View Toggle */
.articles-view-toggle {
  display: flex;
  gap: 5px;
  padding: 10px 20px;
  border-bottom: 1px solid #2d2d44;
  background: #1a1a2e;
}

.articles-view-toggle button {
  flex: 1;
  padding: 10px 20px;
  background: transparent;
  border: 1px solid #3d3d5c;
  border-radius: 8px;
  color: #888;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.articles-view-toggle button:hover {
  border-color: #667eea;
  color: #e0e0e0;
}

.articles-view-toggle button.active {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-color: transparent;
  color: #fff;
}

/* Timeline Layout */
.articles-timeline {
  display: flex;
  flex-direction: column;
  padding: 10px 0;
}

.article-timeline-item {
  display: flex;
  gap: 15px;
  position: relative;
}

.timeline-indicator {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 40px;
  flex-shrink: 0;
  padding-top: 20px;
}

.timeline-dot {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: #3d3d5c;
  border: 3px solid #2d2d44;
  z-index: 2;
  transition: all 0.2s;
  position: relative;
  flex-shrink: 0;
}

.timeline-dot-connected {
  background: #667eea;
  border-color: #a78bfa;
  cursor: pointer;
  box-shadow: 0 0 10px rgba(102, 126, 234, 0.5);
}

.timeline-dot-connected:hover {
  background: #a78bfa;
  transform: scale(1.3);
  box-shadow: 0 0 15px rgba(167, 139, 250, 0.8);
}

.timeline-dot-badge {
  position: absolute;
  top: -8px;
  right: -8px;
  background: #667eea;
  color: #fff;
  font-size: 9px;
  font-weight: 700;
  padding: 2px 5px;
  border-radius: 10px;
  border: 2px solid #1a1a2e;
  min-width: 18px;
  text-align: center;
}

.timeline-line {
  width: 3px;
  flex: 1;
  background: #3d3d5c;
  margin-top: 5px;
  min-height: 40px;
  border-radius: 2px;
  transition: all 0.2s;
}

.timeline-line-strong {
  background: linear-gradient(180deg, #10b981 0%, #059669 100%);
  box-shadow: 0 0 8px rgba(16, 185, 129, 0.4);
}

.timeline-line-moderate {
  background: linear-gradient(180deg, #f59e0b 0%, #d97706 100%);
  box-shadow: 0 0 8px rgba(245, 158, 11, 0.4);
}

.timeline-line-weak {
  background: linear-gradient(180deg, #f97316 0%, #ea580c 100%);
  box-shadow: 0 0 8px rgba(249, 115, 22, 0.4);
}

.timeline-line-minimal {
  background: #3d3d5c;
}

/* Clustered Articles View */
.articles-clusters {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 10px 0;
}

.cluster-divider {
  height: 1px;
  background: linear-gradient(90deg, transparent 0%, #3d3d5c 50%, transparent 100%);
  margin: 10px 0;
}

.article-cluster {
  background: rgba(45, 45, 68, 0.3);
  border: 1px solid #3d3d5c;
  border-radius: 12px;
  overflow: hidden;
}

.cluster-header {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 15px;
  background: #2d2d44;
  cursor: pointer;
  transition: all 0.2s;
}

.cluster-header:hover {
  background: #3d3d5c;
}

.cluster-toggle {
  font-size: 12px;
  color: #888;
  min-width: 12px;
}

.cluster-name {
  flex: 1;
  font-size: 15px;
  font-weight: 700;
  color: #e0e0e0;
}

.cluster-strength {
  font-size: 12px;
  color: #FFA726; /* Warm orange - breaks the purple monotone */
  font-weight: 600;
  padding: 4px 10px;
  background: rgba(255, 167, 38, 0.15); /* Warm orange background */
  border-radius: 12px;
  cursor: pointer; /* Make it feel interactive */
  transition: all 0.2s ease;
  border: 1px solid rgba(167, 139, 250, 0.3);
}

.cluster-articles {
  padding: 15px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* Connection Modal */
.connection-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  animation: fadeIn 0.2s;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.connection-modal {
  background: #1a1a2e;
  border: 1px solid #3d3d5c;
  border-radius: 16px;
  max-width: 500px;
  width: 90%;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  animation: slideUp 0.3s;
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideOut {
  from {
    transform: translateX(0);
    opacity: 1;
  }
  to {
    transform: translateX(100%);
    opacity: 0;
  }
}

.connection-modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #2d2d44;
}

.connection-modal-header h4 {
  margin: 0;
  font-size: 18px;
  color: #e0e0e0;
}

.connection-modal-body {
  padding: 20px;
  overflow-y: auto;
  flex: 1;
}

.connection-articles {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 20px;
  padding: 15px;
  background: #2d2d44;
  border-radius: 12px;
}

.connection-article-name {
  flex: 1;
  font-size: 14px;
  color: #e0e0e0;
  font-weight: 600;
  line-height: 1.4;
}

.connection-arrow {
  font-size: 20px;
  color: #667eea;
  font-weight: 700;
}

.shared-entities-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.shared-entity-item {
  padding: 12px 15px;
  background: #2d2d44;
  border-radius: 8px;
  border-left: 3px solid #667eea;
  font-size: 14px;
  color: #e0e0e0;
  transition: all 0.2s;
}

.shared-entity-item:hover {
  background: #3d3d5c;
  border-left-color: #a78bfa;
  transform: translateX(5px);
}

.connection-modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  padding: 20px;
  border-top: 1px solid #2d2d44;
}

.btn-secondary {
  padding: 10px 24px;
  background: #2d2d44;
  border: 1px solid #3d3d5c;
  border-radius: 8px;
  color: #e0e0e0;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-secondary:hover {
  background: #3d3d5c;
  border-color: #667eea;
  color: #fff;
}

/* Article Entities Panel (Right Sidebar) */
.article-entities-panel {
  background: #1a1a2e;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0;
  border-top: 1px solid #2d2d44;
  flex: 1;
  overflow-y: auto;
}

.article-entities-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #2d2d44;
}

.article-entities-header h3 {
  margin: 0;
  font-size: 16px;
  color: #e0e0e0;
  font-weight: 700;
}

.article-entities-title {
  padding: 15px 20px;
  font-size: 14px;
  color: #a78bfa;
  font-weight: 600;
  line-height: 1.4;
  border-bottom: 1px solid #2d2d44;
  background: rgba(167, 139, 250, 0.05);
}

.article-entities-count {
  padding: 10px 20px;
  font-size: 12px;
  color: #888;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.article-entities-body {
  flex: 1;
  overflow-y: auto;
  padding: 15px 20px;
}

.entity-type-group {
  margin-bottom: 25px;
}

.entity-type-header {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 10px;
}

.entity-type-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  flex-shrink: 0;
}

.entity-type-label {
  font-size: 12px;
  color: #888;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.entity-type-list {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.entity-list-item-clickable {
  padding: 10px 12px;
  background: #2d2d44;
  border: 1px solid #3d3d5c;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.entity-list-item-clickable:hover {
  background: #3d3d5c;
  border-color: #667eea;
  transform: translateX(5px);
}

.entity-list-item-clickable .entity-name {
  font-size: 14px;
  color: #e0e0e0;
  font-weight: 500;
  flex: 1;
}

.entity-list-item-clickable .entity-connections {
  font-size: 11px;
  color: #67e8f9;
  font-weight: 600;
  padding: 2px 8px;
  background: rgba(103, 232, 249, 0.1);
  border-radius: 10px;
  border: 1px solid rgba(103, 232, 249, 0.3);
}

/* Knowledge Navigator */
.navigator-trigger-btn {
  padding: 10px 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border: none;
  border-radius: 8px;
  color: white;
  font-size: 14px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.2s;
  margin-left: 15px;
  box-shadow: 0 2px 8px rgba(102, 126, 234, 0.3);
}

.navigator-trigger-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.5);
}

.navigator-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  animation: fadeIn 0.3s;
}

.navigator-modal {
  background: #1a1a2e;
  border: 1px solid #3d3d5c;
  border-radius: 16px;
  max-width: 700px;
  width: 90%;
  max-height: 85vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.7);
  animation: slideUp 0.3s;
}

.navigator-header {
  padding: 25px 30px;
  border-bottom: 1px solid #2d2d44;
  position: relative;
}

.navigator-header h2 {
  margin: 0 0 8px 0;
  font-size: 24px;
  color: #e0e0e0;
  font-weight: 700;
}

.navigator-subtitle {
  margin: 0;
  font-size: 14px;
  color: #888;
  line-height: 1.5;
}

.navigator-body {
  padding: 30px;
  overflow-y: auto;
  flex: 1;
}

.navigator-inputs {
  display: flex;
  align-items: flex-start;
  gap: 20px;
  margin-bottom: 25px;
}

.navigator-input-group {
  flex: 1;
  position: relative;
}

.navigator-input-group label {
  display: block;
  font-size: 13px;
  font-weight: 600;
  color: #888;
  margin-bottom: 8px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.navigator-input-group input {
  width: 100%;
  padding: 12px 15px;
  background: #2d2d44;
  border: 2px solid #3d3d5c;
  border-radius: 8px;
  color: #e0e0e0;
  font-size: 15px;
  transition: all 0.2s;
}

.navigator-input-group input:focus {
  outline: none;
  border-color: #667eea;
  background: #3d3d5c;
}

.navigator-input-group input:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.navigator-arrow {
  font-size: 28px;
  color: #667eea;
  font-weight: 700;
  margin-top: 32px;
}

.navigator-autocomplete {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background: #2d2d44;
  border: 1px solid #3d3d5c;
  border-radius: 8px;
  margin-top: 5px;
  max-height: 250px;
  overflow-y: auto;
  z-index: 100;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
}

.navigator-autocomplete-item {
  padding: 12px 15px;
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  transition: all 0.2s;
  border-bottom: 1px solid #3d3d5c;
}

.navigator-autocomplete-item:last-child {
  border-bottom: none;
}

.navigator-autocomplete-item:hover {
  background: #3d3d5c;
}

.navigator-autocomplete-empty {
  padding: 20px;
  text-align: center;
  color: #888;
  font-size: 14px;
}

.entity-type-indicator {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  flex-shrink: 0;
}

.entity-type-label {
  margin-left: auto;
  font-size: 11px;
  color: #888;
  text-transform: uppercase;
  font-weight: 600;
}

.navigator-actions {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.navigator-btn {
  padding: 12px 24px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  border: none;
}

.navigator-btn-primary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  flex: 1;
}

.navigator-btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.5);
}

.navigator-btn-primary:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.navigator-btn-secondary {
  background: #2d2d44;
  border: 1px solid #3d3d5c;
  color: #e0e0e0;
}

.navigator-btn-secondary:hover {
  background: #3d3d5c;
  border-color: #667eea;
}

.navigator-error {
  padding: 15px 20px;
  background: rgba(239, 68, 68, 0.1);
  border: 1px solid rgba(239, 68, 68, 0.3);
  border-radius: 8px;
  color: #f87171;
  font-size: 14px;
  line-height: 1.5;
  margin-bottom: 20px;
}

.navigator-result {
  background: rgba(102, 126, 234, 0.05);
  border: 1px solid rgba(102, 126, 234, 0.2);
  border-radius: 12px;
  padding: 20px;
}

.navigator-result-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 15px;
  border-bottom: 1px solid rgba(102, 126, 234, 0.2);
}

.navigator-result-header h3 {
  margin: 0;
  font-size: 18px;
  color: #e0e0e0;
  font-weight: 700;
}

.navigator-result-stats {
  display: flex;
  gap: 10px;
  align-items: center;
  font-size: 13px;
  color: #888;
}

.navigator-path {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.navigator-step {
  display: flex;
  gap: 15px;
  align-items: flex-start;
}

.navigator-step-number {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  font-weight: 700;
  flex-shrink: 0;
}

.navigator-step-content {
  flex: 1;
}

.navigator-step-entity {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px 15px;
  background: #2d2d44;
  border-radius: 8px;
  margin-bottom: 10px;
}

.navigator-step-name {
  font-size: 15px;
  color: #e0e0e0;
  font-weight: 600;
  flex: 1;
}

.navigator-step-type {
  font-size: 11px;
  color: #888;
  text-transform: uppercase;
  font-weight: 600;
}

.navigator-step-relationship {
  display: flex;
  align-items: center;
  gap: 10px;
  padding-left: 15px;
  color: #888;
  font-size: 13px;
}

.relationship-arrow {
  font-size: 18px;
  color: #667eea;
}

.relationship-label {
  flex: 1;
  color: #a78bfa;
  font-weight: 600;
}

.relationship-strength {
  color: #34d399;
  font-weight: 700;
}

/* Timeline Styles */
.graph-container.with-timeline {
  display: flex;
  flex-direction: column;
}

.graph-container.with-timeline > canvas,
.graph-container.with-timeline > svg {
  flex: 1;
  min-height: 0;
}

.timeline-always-visible {
  background: linear-gradient(to top, #0f1419 0%, #1a1f2e 100%);
  border-top: 1px solid rgba(102, 126, 234, 0.2);
  padding: 20px 30px;
  height: 120px;
  display: flex;
  gap: 30px;
  align-items: center;
}

.timeline-left {
  flex: 0 0 35%;
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding-right: 30px;
  border-right: 1px solid rgba(102, 126, 234, 0.2);
}

.timeline-article-title {
  font-size: 15px;
  font-weight: 600;
  color: #e0e0e0;
  line-height: 1.4;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.timeline-article-meta {
  font-size: 12px;
  color: #888;
  font-weight: 500;
}

.timeline-right {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.timeline-slider-track {
  position: relative;
  width: 100%;
  height: 30px;
  margin-bottom: 4px;
}

.timeline-dot {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #667eea;
  border: 2px solid #1a1f2e;
  box-shadow: 0 2px 6px rgba(102, 126, 234, 0.4);
  transition: all 0.2s ease;
  cursor: pointer;
  z-index: 2;
}

.timeline-dot:hover {
  transform: translate(-50%, -50%) scale(1.3);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.6);
  z-index: 3;
}

.timeline-dot.focused {
  background: #fbbf24;
  box-shadow: 0 0 16px rgba(251, 191, 36, 0.8);
  transform: translate(-50%, -50%) scale(1.5);
  z-index: 4;
}

.timeline-slider-range {
  width: 100%;
  height: 6px;
  background: rgba(102, 126, 234, 0.2);
  border-radius: 3px;
  outline: none;
  -webkit-appearance: none;
  appearance: none;
  cursor: pointer;
  position: relative;
  z-index: 1;
}

.timeline-slider-range::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: white;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);
  transition: all 0.2s ease;
}

.timeline-slider-range::-webkit-slider-thumb:hover {
  transform: scale(1.2);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.6);
}

.timeline-slider-range::-moz-range-thumb {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: white;
  cursor: pointer;
  border: none;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);
  transition: all 0.2s ease;
}

.timeline-slider-range::-moz-range-thumb:hover {
  transform: scale(1.2);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.6);
}

.timeline-labels-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 11px;
  color: #888;
}

.timeline-label {
  font-weight: 500;
}

.timeline-position {
  font-size: 12px;
  color: #a78bfa;
  font-weight: 600;
}

/* Processing Article States */
.article-card-processing {
  opacity: 0.7;
  background: rgba(99, 102, 241, 0.03);
  border-color: rgba(99, 102, 241, 0.2);
}

.timeline-dot-processing {
  background: var(--color-primary);
  border: 2px solid var(--color-primary);
  animation: processingPulse 1.5s ease-in-out infinite;
}

.processing-spinner {
  width: 8px;
  height: 8px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-top: 1px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.processing-status {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 0;
}

.processing-message {
  font-size: 13px;
  color: var(--color-primary);
  font-style: italic;
  font-weight: 500;
}

.processing-indicator {
  display: flex;
  align-items: center;
}

.pulse-dot {
  width: 6px;
  height: 6px;
  background: #FFA726; /* Subtle warm orange - just a hint */
  border-radius: 50%;
  animation: pulse 1.5s ease-in-out infinite;
}

@keyframes processingPulse {
  0%, 100% {
    opacity: 0.6;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.1);
  }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

@keyframes pulse {
  0%, 100% {
    opacity: 0.4;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.2);
  }
}
